:root {
  --font-name: "...." ;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: --font-name;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:active,
a:hover {
  text-decoration: none;
}

h1 {
  color: red;
  font-size: em(800);
}